import 'popper.js';
import 'bootstrap';
import 'jquery';
import './scss/styles.scss';

import Prism from 'prismjs';
import 'paginationjs';

let workshopTemplate = require('./hbs/handlebars/workshop.handlebars');

// Import PrismJS extensions
(function () {
    'use strict';

    $(function () {


        // Workshops
        $('.workshop-pagination').pagination({
            dataSource: function (done) {

                $.ajax({
                    type: 'GET',
                    url: '../code/workshop.json',
                    success: function (response) {
                        done(response.events);
                    }
                })

            },
            pageSize: 6,
            ulClassName: 'pagination justify-content-center',
            prevText: '&lsaquo;',
            nextText: '&rsaquo;',
            callback: function (data, pagination) {

                const html = workshopTemplate({
                    workshops: data
                });

                $('.workshops-items .row').html(html);

                // Workshops tooltips
                $('[data-toggle="popover"]').popover({
                    html: true,
                    content: function () {
                        const content = $(this).attr('data-popover-content');
                        return $(content).children('.popover-body').html();
                    }
                })
            }
        });


        // Prism JS
        setTimeout(() => {
            Prism.highlightAll();
        }, 0);

        // Defensive by design menu
        const $menuItemsContainer = $('.snippets-code__languages');
        const $snippets = $('.snippets-code__wrap');

        if ($menuItemsContainer) {

            $menuItemsContainer.on('click', 'a', function (event) {
                const $self = $(this);
                event.preventDefault();

                $menuItemsContainer.find('li').removeClass('active');
                $self.parent('li').addClass('active');


                const snippetLang = $self.data('snippet');

                $snippets.each(function (index, item) {
                    $(item).addClass('d-none');
                });

                $snippets.filter(`[data-code='${snippetLang}']`).removeClass('d-none');

            });
        }
    });



}());

//Contact form
function register($form) {
  jQuery.ajax({
    type: "GET",
    url: "https://secdim.us19.list-manage.com/subscribe/post-json?u=38a83605383670ee5065ba7b3&amp;id=da08b743cf&c=?",
    data: $form.serialize(),
    cache       : false,
    dataType    : 'jsonp',
    contentType: "application/json; charset=utf-8",
    error       : function(err) { console.log('error') },
    success     : function(data) {
      if (data.result != "success") {
          console.log('contactform fail');
          $("#contactsubmit").remove();
          $("#contactfail").removeClass("invisible");
      } else {
          console.log('contactform success');
          $("#contactsubmit").remove();
          $("#contactsuccess").removeClass("invisible");
      }
    }
  });
}

jQuery(document).on('submit', '#contactform', function(event) {
    try {
      var $form = jQuery(this);
      event.preventDefault();
      register($form);
    } catch(error){}
});
